@import url(./__title/login__title.css);
@import url(./__input/login__input.css);
@import url(./__link/login__link.css);
@import url(./__button/login__button.css);

.login {
    position: relative;
    width: 358px;
    margin-top: 60px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 550px) {
    .login {
        width: 260px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}